<template>
  <div class="investigate">
    <header>
      <i class="icon icon_back"></i>
      <title>个人税收身份声明信息</title>
    </header>
    <div class="iconBox">
      <i class="icon_logo"></i>
      <i class="icon_form"></i>
    </div>
    <div class="form_Infor">
      <div class="infor_title">
        <i class="icon_infor"></i>
        请输入个人所得税身份信息：
        <cm-input
          label="姓名"
          @update="
            v => {
              this.username = v;
            }
          "
          type="type1"
          :value="username"
        ></cm-input>
        <cm-select
          @select="selectSotps"
          title="选择纳税人身份"
          :options="sotps"
        ></cm-select>
        <div v-if="sotpsType == 2 || sotpsType == 3" class="showBox">
          <cm-select
            type="type2"
            :options="addRess"
            title="现居住地址"
            label="所在地区："
          ></cm-select>
          <cm-input
            @update="
              v => {
                this.address = v;
              }
            "
            label="请填写街道、楼牌号"
            type="type3"
            title="详情地址："
            :value="address"
          ></cm-input>
          <cm-select
            type="type2"
            :options="birthplaces"
            title="出生地"
            label="所在地区："
          ></cm-select>
          <cm-input
            @update="
              v => {
                this.address = v;
              }
            "
            label="请填写街道、楼牌号"
            type="type3"
            title="详情地址："
            :value="address"
          ></cm-input>
          <cm-select
            v-if="sotpsType != 3"
            @select=""
            title="税收居民国"
            :options="trtctrys"
          ></cm-select>
        </div>
        <div class="noEdit" style="width:100%">
          <cm-input
            v-if="sotpsType == 1 || !sotpsType"
            @update="
              v => {
                this.tpifnb = v;
              }
            "
            label="请填写"
            type="type2"
            title="纳税人识别号"
            :value="tpifnb"
          ></cm-input>
        </div>
        <cm-input
          v-if="sotpsType == 2"
          @update="
            v => {
              this.tpifnb = v;
            }
          "
          label="请填写"
          type="type2"
          title="纳税人识别号"
          :value="tpifnb"
        ></cm-input>
        <cm-input
          v-if="sotpsType == 3"
          @update="
            v => {
              this.tpifnb = v;
            }
          "
          label="请填写"
          type="type2"
          title="中国纳税人识别号"
          :value="tpifnb"
        ></cm-input>
        <div v-if="sotpsType == 2 || sotpsType == 3" class="cause_box">
          <div v-if="sotpsType != 3" class="title">
            如不能提供居民国（地区）纳税人识别号，请选择原因：
          </div>
          <div v-if="sotpsType != 3" class="select_radio">
            <van-radio-group v-model="selectCause">
              <van-radio name="1">居民国（地区）不发放纳税人识别号</van-radio>
              <van-radio name="2">账户持有人未能取得纳税人识别号</van-radio>
            </van-radio-group>
          </div>
          <cm-select
            v-if="otherCountry"
            @select=""
            title="其他税收居民国"
            :options="otherTrtctrys"
          ></cm-select>
          <cm-select
            v-if="sotpsType == 3"
            @select=""
            title="其他税收居民国"
            :options="trtctrys"
          ></cm-select>
          <cm-input
            v-if="otherCountry || sotpsType == 3"
            @update="
              v => {
                this.tpifnb = v;
              }
            "
            label="请填写"
            type="type2"
            title="其他国家纳税人识别号"
            :value="tpifnb"
          ></cm-input>
          <div v-if="otherCountry || sotpsType == 3" class="title">
            如不能提供居民国（地区）纳税人识别号，请选择原因：
          </div>
          <div v-if="otherCountry || sotpsType == 3" class="select_radio">
            <van-radio-group v-model="selectCause">
              <van-radio name="1">居民国（地区）不发放纳税人识别号</van-radio>
              <van-radio name="2">账户持有人未能取得纳税人识别号</van-radio>
            </van-radio-group>
          </div>
          <div class="texttarea_box">
            <van-field
              v-model="message"
              rows="3"
              autosize
              type="textarea"
              placeholder="请填写具体原因"
              show-word-limit
            />
          </div>
          <div
            v-if="otherCountry"
            @click="otherCountry = false"
            class=" btn_del"
          >
            <van-icon name="delete-o" />删除其他国家信息
          </div>
          <div class="title">
            若您仍是其他国家（地区）税收居民，请点击下 方按钮，继续填写
          </div>
        </div>
        <div
          class="btn_sure"
          v-if="sotpsType == 2 || sotpsType == 3"
          @click="otherCountry = true"
        >
          仍是其他国家（地区）税收居民
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="title">
        <van-icon
          name="info-o"
        />本人确认上述信息的真实、准确和完整，且当这些信息
        发生变更时，将在30日内通知我司，否则本人承担由此 造成的不利和后果。
      </div>
      <div class="vtn_upload">
        确认提交
      </div>
    </div>
  </div>
</template>

<script>
import cmInput from "../components/cmInput.vue";
import cmSelect from "../components/cmSelect.vue";
export default {
  components: {
    cmInput,
    cmSelect
  },
  data() {
    return {
      username: "",
      //纳税人身份
      sotpsType: 0,
      sotps: [
        {
          text: "01-仅为中国税收居民",
          value: 1
        },
        {
          text: "02-非居民",
          value: 2
        },
        {
          text: "03-既是中国又是其他国家（地区）税收居民",
          value: 3
        }
      ],
      //纳税人识别号
      tpifnb: "",
      addRess: [],
      address: "",
      //出生地
      birthplaces: [],
      birthplace: "",
      //税收居民国
      trtctrys: [],
      trtctry: "",
      otherCountry: false,
      selectCause: 0,
      message: "",
      otherTrtctrys: [],
      otherTrtctry: ""
    };
  },
  methods: {
    selectSotps(value) {
      this.sotpsType = value[0].value;
      console.log(this.sotpsType);
    }
  },
  created() {},
  mounted() {}
};
</script>
<style lang="scss" scoped>
/* @import url(); 引入css类 */
.investigate {
  max-width: 100vw;
  width: 100vw;
  overflow: hidden;
  background: #eef1fa;
  min-height: 100vh;
  box-sizing: border-box;
  font-family: PingFang SC;
  header {
    width: 100vw;
    height: 88px;
    background: #fff;
    .icon_back {
      background: url("../assets/imgs/path.png");
      background-size: 100%;
      background-repeat: no-repeat;
      width: 20px;
      height: 35px;
      display: inline-block;
      position: absolute;
      left: 35px;
      top: 27px;
    }
    title {
      display: inline-block;
      font-size: 32px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #111111;
      margin-left: 50%;
      line-height: 88px;
      transform: translate(-50%, 0);
    }
  }
  .iconBox {
    width: 100%;
    height: 249px;
    position: relative;
    .icon_logo {
      width: 305px;
      height: 81px;
      background: url("../assets/imgs/icon_1.png");
      display: inline-block;
      position: absolute;
      left: 79px;
      top: 89px;
      background-repeat: no-repeat;
    }
    .icon_form {
      width: 230px;
      height: 220px;
      background: url("../assets/imgs/baodan_03.png");
      display: inline-block;
      position: absolute;
      left: 440px;
      bottom: 0px;
      z-index: 1;
      background-repeat: no-repeat;
    }
  }
  .form_Infor {
    width: 686px;
    margin: 0 auto;
    box-sizing: border-box;
    background: #fff;
    box-shadow: 0px 6px 21px 8px rgba(0, 0, 0, 0.05);
    border-radius: 16px;
    z-index: 11;
    padding: 34px 32px;
    .icon_infor {
      width: 35px;
      height: 27px;
      display: inline-block;
      background: url("../assets/imgs/personId.png");
      margin-right: 9px;
      background-repeat: no-repeat;
      position: relative;
      top: 2px;
    }
    .infor_title {
      font-size: 30px;
      font-weight: bold;
      color: #252525;
      line-height: 34px;
    }
  }
  .input_type1 {
    width: 100%;
    margin-top: 61px;
    padding-bottom: 38px;
    border-bottom: 1px solid #ebebeb;
  }
  .showBox {
    width: 100%;
  }
  .cause_box {
    width: 100%;
    .title {
      margin-top: 38px;
      font-size: 30px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #8a8a8a;
      line-height: 38px;
    }
  }
  .texttarea_box {
    border: 2px solid #ebebeb;
    border-radius: 8px;
    padding: 23px;
    width: 100%;
    box-sizing: border-box;
    margin-top: 30px;
    margin-bottom: 37px;
  }
  .btn_sure {
    width: 590px;
    height: 104px;
    background: #4181ff;
    border-radius: 8px;
    margin: 0 auto;
    font-size: 32px;
    font-family: PingFang SC;
    font-weight: 500;
    color: #ffffff;
    line-height: 104px;
    text-align: center;
    margin-top: 39px;
    margin-bottom: 57px;
  }
  .btn_del {
    width: 590px;
    height: 104px;
    background: #fff;
    border-radius: 8px;
    margin: 0 auto;
    font-size: 32px;
    font-family: PingFang SC;
    font-weight: 500;
    line-height: 104px;
    text-align: center;
    margin-top: 39px;
    margin-bottom: 57px;
    border: 2px solid #4181ff;
    color: #4181ff;
  }
}
.bottom {
  box-sizing: border-box;
  width: 100%;

  padding: 64px 32px 97px 32px;
  .van-icon-info-o {
    position: relative;
    top: 2px;
    margin-right: 7px;
  }
  .title {
    height: 97px;
    font-size: 26px;
    font-family: PingFang SC;
    font-weight: 500;
    color: #8a8a8a;
    line-height: 26px;
    margin-right: 0;
  }
  .vtn_upload {
    width: 686px;
    height: 104px;
    background: #4f82fa;
    border-radius: 8px;
    font-size: 36px;
    font-family: PingFang SC;
    font-weight: 500;
    color: #ffffff;
    line-height: 104px;
    text-align: center;
    margin-top: 54px;
  }
}
/deep/ {
  .cause_box {
    .van-radio-group {
      margin-top: 40px;
    }
    .van-radio__label {
      font-size: 30px;
      font-family: PingFang SC;
      font-weight: 500;
      color: #252525;
      line-height: 48px;
    }
    .van-radio__icon {
      line-height: 48px;
      height: 48px;
    }
    .van-icon-success {
      font-size: 30px;
      position: relative;
      top: 6px;
      margin-right: 15px;
    }
    .van-cell {
      padding: 0;
    }
    .van-field__body .van-field__control {
      line-height: 48px;
      font-size: 26px;
      font-weight: 500;
    }
    .textarea {
      padding: 23px 24px;
    }
  }
  .van-icon-delete-o {
    position: relative;
    top: 4px;
    margin-right: 7px;
  }
  [class*="van-hairline"]::after {
    border: none;
  }
  .van-cell::after {
    border: none;
  }
  .noEdit {
    .van-field__left-icon {
      display: none;
    }
    .van-field__control {
      font-weight: 500;
      &::placeholder {
        color: #bababa;
      }
    }
  }
  .van-field__control {
    font-weight: 500;
  }
  .van-cascader__option--selected {
    color: #4f82fa;
  }
}
</style>



